@import "smart-grid.sass";
@import "fontawesome";
@import url('https://fonts.googleapis.com/css?family=Montserrat:500|Nunito+Sans:400,700|Work+Sans');

@include reset();
body {
    background-color: #f5f5f5; }

h2 {
    font-family: 'Montserrat', sans-serif;
    font-size: 38px; }


.h2sub {
    text-align: center;
    font-size: 18.2px;
    margin: 25px 0;
    font-family: 'Nunito Sans', regular;
    line-height: 1.5; }


.wrapper {
    @include wrapper(); }
.wrapper-full {
    @include wrapper-full(); }

.portfolio {
    background: #f6f6f6;
    padding: 70px 0 70px;

    h2, .h2sub {
        color: #000; } }
.portfolio-h2 {
    text-align: center; }
.navbar-area {
    display: flex;
    flex-flow: row wrap;
    margin: auto;
    padding-top: 38px;
    padding-bottom: 34px; }


.navbar-component {
    background-color: #fff; }


.navbar-component,.navbar-area {
    justify-content: space-between; }

.brand {
    margin-left: 44px; }
.list {
    margin-right: 45px;
    a {
        padding-left: 22px;
        width: 211px;
        height: 9px;
        color: #191919;
        font-family: Montserrat;
        font-size: 12px;
        font-weight: 500;
        line-height: 24px;
        text-transform: uppercase;
        letter-spacing: 1.2px;
        &:hover {
            color: #10c9c3; } } }
.photo {
    background-color: #000; }
.photo-item {
    display: flex;
    flex-wrap: wrap; }
.photo-items {
    width: 339px;
    height: 300px;
    background-color: #838383;
    position: relative;
    a {
        display: block;
        transition: 0.5s;
        position: absolute;
        img {
            margin: 132px 145px;
            display: none; } }

    &:hover {
        background-color: #10c9c3;
        opacity: 0.85;
        transition: 0.5s;
        img {
            display: block; } } }
.new-color {
    background-color: #ddd; }
.smal {
    width: 290px;
    height: 300px;
    a {
        display: block;
        transition: 0.5s;
        position: absolute;
        img {
            margin: 132px 112px;
            display: none; } }
    &:hover {
        background-color: #10c9c3;
        opacity: 0.85;
        transition: 0.5s;
        img {
            display: block; } } }
.smal-end {
    width: 295px; }

.contact-content {
    display: flex;
    height: 535px;
    margin-top: 70px;
    background-color: #f5f5f5; }
.contact-info {
    width: 369px;
    height: 306px;
    h1 {
        color: #171717;
        font-family: Montserrat;
        font-size: 30px;
        font-weight: 500;
        line-height: 84px; } }
.contact-info-content,.contect-info-text {
    color: #787878;
    font-family: "Nunito Sans";
    font-size: 16px;
    font-weight: 400; }
.contact-info-content {
    letter-spacing: 0.08px;
    line-height: 24px;
    margin-top: 6px; }
.contect-info-text {
    line-height: 36px;
    font-weight: 400;
    letter-spacing: 0.08px;
    margin-top: 19px;
    span {
        color: #171717;
        font-weight: 700;
        letter-spacing: 0.08px; } }
.contact-form-bt {
    width: 671px;
    height: 410px;
    margin-top: 27px;
    margin-left: 127px; }
.contact-form {
    height: 326px; }
.contact-form-name,.contact-form-email,.contact-form-title {
    height: 43px;
    background-color: #fff; }
.contact-form-name {
    width: 297px;
    padding-left: 19px; }
.contact-form-email {
    width: 300px;
    margin-left: 25px;
    padding-left: 18px; }
.contact-form-title {
    width: 650px;
    margin-top: 30px;
    padding-left: 18px; }
.contact-form-content {
    width: 650px;
    height: 153px;
    background-color: #fff;
    margin-top: 30px;
    resize: none;
    padding-left: 18px;
    padding-top: 16px;
    margin-bottom: 43px; }
.contact-form-submit {
    color: white;
    padding: 19px 34px;
    font-family: 'Montserrat';
    text-transform: uppercase;
    letter-spacing: 1.2px;
    font-size: 12px;
    font-weight: 500;
    background-color: #10c9c3; }
#map {
    width: 1170px;
    height: 470px;
    margin: auto;
    margin-bottom: 100px; }
.baner-content {
    img {
        position: relative;
        width: 100%; }
    h2 {
        position: absolute;
        top: 210px;
        left: 0;
        right: 0;
        margin: auto;
        letter-spacing: 0.38px;
        color: #fff;
        font-family: Montserrat;
        font-size: 38px;
        font-weight: 500;
        line-height: 48px; } }
.baner-h2 {
    text-align: center; }
.h2subbaner {
    color: #fff;
    position: absolute;
    top: 281px;
    left: 0;
    right: 0;
    margin: auto;
    font-family: "Nunito Sans";
    font-size: 18px;
    font-weight: 400;
    line-height: 28px;
    width: 512px;
    height: 44px;
    text-align: center;
    letter-spacing: 0.05px; }
.number {
    background-color: #10c9c3;
    margin-top: -4px; }
.number-content {
    @include row-flex(); }
.number-item {
    @include col();
    @include size(3);
    display: flex;
    padding-top: 94px;
    padding-bottom: 101px;
    img {
        align-self: center; }
    span {
        color: #fff;
        font-family: Montserrat;
        font-size: 18px;
        font-weight: 500;
        line-height: 30px; }
    p {
        padding-left: 30px;
        display: block;
        text-transform: uppercase;
        color: #fff;
        font-family: Montserrat;
        font-size: 12px;
        font-weight: 500;
        letter-spacing: 1.2px; } }
.about-content {
    @include row-flex(); }
.about-content-text {
    @include col();
    @include size(6);
    img {
        margin-top: 35px;
        margin-left: 125px; } }
.about-content-h2 {
    margin-left: 125px;
    margin-top: 110px;
    font-size: 30px;
    font-weight: 500;
    line-height: 84px; }
.about-content-p {
    margin-left: 125px;
    width: 454px;
    color: #787878;
    font-family: "Nunito Sans";
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0.08px; }
.need-project-content {
    position: relative;
    height: 325px;
    margin-top: 74px;
    h3 {
        color: #1d1d1d;
        font-family: Montserrat;
        font-size: 30px;
        font-weight: 600;
        line-height: 84px;
        text-align: center; }
    p {
        padding-top: 2px;
        text-align: center;
        margin-bottom: 51px;
        letter-spacing: 0.18px;
        color: #787878;
        font-family: "Nunito Sans";
        font-size: 16px;
        font-weight: 400;
        line-height: 24px; }
    a {
        color: white;
        padding: 19px 50px;
        font-family: 'Montserrat';
        text-transform: uppercase;
        letter-spacing: 1.2px;
        font-size: 12px;
        font-weight: 500;
        background-color: #10c9c3;
        margin: 60px 540px; } }
.footer {
    background-color: #141414;
    height: 284px; }
.content {
    display: flex; }
.piroll {
    width: 225px;
    height: 84px;
    display: block;
    margin-top: 68px;
    margin-left: -2px; }
.piroll1 {
    width: 175px;
    height: 18px;
    color: #fff;
    font-family: Montserrat;
    font-size: 18px;
    font-weight: 500;
    line-height: 72px; }
.piroll2 {
    width: 225px;
    height: 39px;
    color: #bcbcbc;
    font-family: "Nunito Sans";
    font-size: 15px;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0.07px;
    padding-top: 50px; }
.mail {
    width: 153px;
    height: 35px;
    color: #bcbcbc;
    font-family: "Nunito Sans";
    font-size: 15px;
    font-weight: 400;
    line-height: 24px;
    margin-top: 90px;
    margin-left: 246px;
    letter-spacing: 0.07px; }
.text1,.text2,.text3 {
    ul {
        width: 57px;
        height: 83px;
        font-family: "Nunito Sans";
        font-size: 15px;
        font-weight: 400;
        line-height: 24px;
        margin-top: 90px;
        a {
            color: #bcbcbc;
            &:hover {
                color: #10c9c3; } } } }
.text1 {
    margin-left: 145px; }
.text2 {
    margin-left: 55px; }
.text3 {
    margin-left: 132px; }
